export default {
  name: 'Campus',
  data () {
    return {
      key: '',
      roomList: [],
      info: {}
    }
  },
  created () {
    this.key = this.$route.query.key
    this.getData()
  },
  methods: {
    getData () {
      var that = this
      var formdata = new URLSearchParams()
      formdata.append('key', this.key)
      that.axiosPost('/live/campus', formdata).then((res) => {
        that.info = res.data
        that.$route.meta.title = res.data.title
        that.roomList = res.data.room_list
      })
    },
    goRoom (item) {
      this.$router.push({path: '/room', query:{room_id:item.id, from:"groupmessage", isappinstalled:0}})
    }
  }
}



